<script>
import saveAs from 'file-saver';
import html2canvas from 'html2canvas';

const page_title = '만다라트 계획표';
let mandalart = [
  ["", "", "", "", "", "", "", "", ""],
  ["", "", "", "", "", "", "", "", ""],
  ["", "", "", "", "", "", "", "", ""],
  ["", "", "", "목표1", "목표4", "목표6", "", "", ""],
  ["", "", "", "목표2", "최종목표", "목표7", "", "", ""],
  ["", "", "", "목표3", "목표5", "목표8", "", "", ""],
  ["", "", "", "", "", "", "", "", ""],
  ["", "", "", "", "", "", "", "", ""],
  ["", "", "", "", "", "", "", "", ""]
];
let hover_minus = [
  [true, true, true, true, true, true, true, true, true],
  [true, true, true, true, true, true, true, true, true],
  [true, true, true, true, true, true, true, true, true],
  [true, true, true, true, true, true, true, true, true],
  [true, true, true, true, true, true, true, true, true],
  [true, true, true, true, true, true, true, true, true],
  [true, true, true, true, true, true, true, true, true],
  [true, true, true, true, true, true, true, true, true],
  [true, true, true, true, true, true, true, true, true]
];
const bg_colors = [
  ["goal-1", "goal-1"         , "goal-1"       , "goal-4"                   , "goal-4"               , "goal-4"                      , "goal-6"    , "goal-6"         , "goal-6"],
  ["goal-1", "goal-1 sub-goal", "goal-1"       , "goal-4"                   , "goal-4 sub-goal"      , "goal-4"                      , "goal-6"    , "goal-6 sub-goal", "goal-6"],
  ["goal-1", "goal-1"         , "goal-1"       , "goal-4 right"             , "goal-4 right"         , "goal-4 right"                , "goal-6"    , "goal-6"         , "goal-6"],
  ["goal-2", "goal-2"         , "goal-2 bottom", "goal-1 sub-goal top left" , "goal-4 sub-goal left" , "goal-6 sub-goal left bottom" , "goal-7 top", "goal-7"         , "goal-7"],
  ["goal-2", "goal-2 sub-goal", "goal-2 bottom", "goal-2 sub-goal top"      , "main-goal"            , "goal-7 sub-goal bottom"      , "goal-7 top", "goal-7 sub-goal", "goal-7"],
  ["goal-2", "goal-2"         , "goal-2 bottom", "goal-3 sub-goal top right", "goal-5 sub-goal right", "goal-8 sub-goal right bottom", "goal-7 top", "goal-7"         , "goal-7"],
  ["goal-3", "goal-3"         , "goal-3"       , "goal-5 left"              , "goal-5 left"          , "goal-5 left"                 , "goal-8"    , "goal-8"         , "goal-8"],
  ["goal-3", "goal-3 sub-goal", "goal-3"       , "goal-5"                   , "goal-5 sub-goal"      , "goal-5"                      , "goal-8"    , "goal-8 sub-goal", "goal-8"],
  ["goal-3", "goal-3"         , "goal-3"       , "goal-5"                   , "goal-5"               , "goal-5"                      , "goal-8"    , "goal-8"         , "goal-8"]
];
const blocked_cells = [
	'1:1',
	'1:4',
	'1:7',
	'4:1',
	'4:7',
	'7:1',
	'7:4',
	'7:7'
];
let json_filename = '';
let temp_list = [];
let temp_item = '';
let selected = -1;
let temp_idea = '';

const handleFileInput = async (event) => {
	const file = event.target.files[0];
	const reader = new FileReader();

	reader.onload = (e) => {
		const user_data = JSON.parse(e.target.result);
		if(user_data.mandalart) {
			mandalart = user_data.mandalart;
			temp_list = user_data.temp_list;
		} else {
			mandalart = user_data;
		}
	};

	reader.readAsText(file);
};

const handleSave = () => {
	const user_data = {
		mandalart,
		temp_list
	}
	const json = JSON.stringify(user_data);
	const blob = new Blob([json], { type: 'application/json' });
	const file_name = (json_filename || 'my-mandalart') + '.json';
	saveAs(blob, file_name);
	saveAsImage();
};

function saveAsImage() {
  const div = document.querySelector('#my-div');
  html2canvas(div).then(canvas => {
    const link = document.createElement('a');
    link.href = canvas.toDataURL();
    link.download = (json_filename || 'my-mandalart') + '.png';
    link.click();
  });
}
function isAvailableCell(ri,ci) {
	const current_position = `${ri}:${ci}`;
	const blocked_cell = blocked_cells.find(cell => cell == current_position);
	return (!blocked_cell)
}
function tdFocus(e,ri,ci) {
	const el = e.target
	const is_available = isAvailableCell(ri,ci);
	if(temp_item.length > 0 && is_available) {
		const current_item = mandalart[ri][ci];
		if(current_item.length > 0) {
			temp_list = [...temp_list, current_item];
		}
		mandalart[ri][ci] = temp_item;
		refreshList(temp_item);
		temp_item = '';
	} else {
		const range = document.createRange();
		range.selectNodeContents(el);
		const sel = window.getSelection();
		sel.removeAllRanges();
		sel.addRange(range);
	}
    // document.execCommand("copy");
}
function getBack(ri, ci) {
	const current_item = mandalart[ri][ci];
	const is_available = isAvailableCell(ri,ci);
	
	if(current_item.length > 0 && is_available) {
		temp_item = current_item;
		temp_list = [...temp_list, current_item];
		mandalart[ri][ci] = '';
	}
}
function refreshList(pasted_item) {
	const temp_array = temp_list.filter(item => item != pasted_item);
	temp_list = [...temp_array];
	selected = -1;
}
function saveThis(e) {
	if (e.charCode === 13) {
		temp_list = [...temp_list, temp_idea];
		temp_idea = '';
	}
}

$: mandalart[1][1] = mandalart[3][3]; // 목표1
$: mandalart[1][4] = mandalart[3][4]; // 목표2
$: mandalart[1][7] = mandalart[3][5]; // 목표3
$: mandalart[4][1] = mandalart[4][3]; // 목표4
$: mandalart[4][7] = mandalart[4][5]; // 목표5
$: mandalart[7][1] = mandalart[5][3]; // 목표6
$: mandalart[7][4] = mandalart[5][4]; // 목표7
$: mandalart[7][7] = mandalart[5][5]; // 목표8
$: json_filename = mandalart[4][4].replace(/<[^>]*>/g, ' ');; // 최종목표

</script>

<svelte:head>
	<title>{page_title}</title>
</svelte:head>
<header>
	<h1>{page_title}</h1>
</header>
<div>
	만다라트 파일 불러오기:
	<input type="file" accept=".json" on:change={handleFileInput} />
	<button on:click={handleSave}>파일로 저장하기</button>
</div>
<div class="content">
	<div id="my-div">
		<div class="goal-title">
			<h2>{json_filename}</h2>
		</div>
		<div class="mandalart">
			{#each mandalart as row, ri}
			<div>
				{#each row as cell, ci}
				<div class:each-block={true}
					class={bg_colors[ri][ci]}
					on:focus={()=>{}}
					on:mouseover={()=>{hover_minus[ri][ci] = false;}}
					on:mouseleave={()=>{hover_minus[ri][ci] = true;}}
				>
					<div
						contenteditable="true"
						on:focus={(e)=>{tdFocus(e,ri,ci)}}
						bind:innerHTML={cell}
					>
					</div>
					<div class="hover-minus"
						class:hidden={hover_minus[ri][ci]}>
						<button on:click={()=>{getBack(ri,ci);}}>↸</button>
					</div>
				</div>
				{/each}
			</div>
			{/each}
		</div>
	</div>
</div>
<div class="top-left">
	<div class="temp-header">
		<h3>아이디어 보관함</h3>
		<input type="text" bind:value={temp_idea} on:keypress={saveThis} placeholder="텍스트 입력 후 엔터"/>
	</div>
	<ul>
		{#each temp_list as row, ri}
			<li>
				<button
					class:selected={selected == ri}
					on:click={()=>{temp_item = row; selected = ri}}>{@html row}</button>
			</li>
		{/each}
	</ul>
</div>

<style>
:global(body) {
	text-align: center;
}
.content {
	display: flex;
	align-items: center;
	justify-content: center;
}
#my-div {
	width: fit-content;
}
.mandalart {
	display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: 8px;
}
[contenteditable] {
	display: grid;
	padding: 0.5em;
	width: 90px;
	height: 90px;
	overflow: hidden;
	text-align: center;
	align-items: center;
}
.each-block {
	position: relative;
	border: 1px solid rgb(171, 171, 171);
	display: flex;
}
.hover-minus {
	position: absolute;
	top: 2px;
	left: 2px;
}
h3 {
	text-align: center;
}
li {
	cursor: pointer;
}
.hidden {
	display: none;
}
.selected {
	background-color: yellowgreen;
}
.temp-header {
	width: 100%;
	text-align: center;
}
.top {
	border-top: 1px double;
}
.left {
	border-left: 1px double;
}
.right {
	border-right: 1px double;
}
.bottom {
	border-bottom: 1px double;
}
.main-goal {
	font-weight: bold;
	color: purple;
}
.sub-goal {
	font-weight: bold;
	color: orangered;
}
.goal-1 {
	background-color: rgb(255, 255, 202);
}
.goal-2 {
	background-color: rgb(211, 251, 211);
}
.goal-3 {
	background-color: rgb(253, 231, 217);
}
.goal-4 {
	background-color: rgb(219, 241, 255);
}
.goal-5 {
	background-color: rgb(241, 216, 254);
}
.goal-6 {
	background-color: rgb(255, 212, 228);
}
.goal-7 {
	background-color: rgb(207, 225, 255);
}
.goal-8 {
	background-color: rgb(234, 248, 158);
}
.top-left {
	position: fixed;
	top: 0;
	left: 0;
	width: 300px;
	height: 1000px;
	overflow-y: auto;
	text-align: left;
	padding: 5px;
	border: 5px solid blue;
	background-color: rgb(242, 239, 239);
}
</style>
